const turf = require('@turf/turf');
export function totalFloorArea(polyCollection) {
    const floorHeight = 3;
    const areas = { 
        total: 0,
        floors: [] 
    }

    turf.featureEach(polyCollection, p => {
        let area = turf.area(p);
        let floors = Math.floor(p.properties.height / floorHeight);
        for (let i = 0; i < floors; i++) {
            areas.floors[i] = (areas.floors[i] || 0) + area
        }
        areas.total = areas.total + area * floors;
    });
    return areas;
}

export function makeZiggurat(selectedParcels, setbacks, minFloorArea = 100) {
    const out = [];
    selectedParcels.forEach(p => {
        // how do we filter out junk parcels?
        // if (p.properties.PARCEL_STATUS !== 'A') {
        //     return;
        // }
        // Z_LEVEL

        let outside = p;
        for (let setback of setbacks) {
            if (!setback.x) {
                outside.properties = { height: setback.h }; // warning, modifying object
                out.push(outside);
                break;            
            }
            let inside = turf.buffer(outside, -setback.x, { units: 'meters' });
            if (inside && turf.area(inside) > minFloorArea) {
                let strip = turf.difference(outside, inside);
                strip.properties = { height: setback.h };
                out.push(strip);
            } else {
                outside.properties = { height: setback.h }; // warning, modifying object
                out.push(outside);
                break;
            }
            outside = inside;
        }
    });
    console.log('Total setback polygons: ' + out.length);
    return turf.featureCollection(out);
}
