<template lang="pug">
    #app
        //- #top
        //-     h3 Ziggurat Computer
        #middle
            #sidebar
                h2 Ziggurat Computer
                //- h3 Draw your precinct
                | Use the polygon tool to select your precinct of interest.
                #setbacks
                    h3 Define the setbacks
                    .setback(v-for="setback in setbacks")
                        | &rarr;
                        input.horizontal(v-model.number="setback.x")
                        | m&nbsp; &nbsp;&nbsp;
                        | Height:
                        input.vertical(v-model.number="setback.h")
                        | m
                    //- .setback
                        | Default vertical
                        input.vertical
                    br
                    .min-floor-area
                        label Min. area per floor &nbsp;
                        input(v-model.number="minFloorArea")
                        | m²
                    br
                    button#recalculate(v-on:click="recalculate()") Recalculate!
                #total(v-if="floorArea")
                    h2 Developable floor area
                    h3 Separate lots
                    .floor(v-for="(area, floor) in floorArea.floors")
                        span.floor-num {{ floor ? 'Floor ' + floor : 'Ground' }}:
                        span.floor-area {{  Math.round(area).toLocaleString() }} m²
                    .floor-total
                        span.floor-num Total
                        span.floor-area {{  Math.round(floorArea.total).toLocaleString() }} m²
                    .checkbox
                        input(type="checkbox" id="showSeparate" v-model="showSeparate")
                        label(for="showSeparate") Show on map
                    h3 After amalgamation
                    .floor(v-for="(area, floor) in amalgamatedFloorArea.floors")
                        span.floor-num {{ floor ? 'Floor ' + floor : 'Ground' }}:
                        span.floor-area {{  Math.round(area).toLocaleString() }} m²
                    .floor-total
                        span.floor-num Total
                        span.floor-area {{  Math.round(amalgamatedFloorArea.total).toLocaleString() }} m²
                    .checkbox
                        input(type="checkbox" id="showAmalgamated" v-model="showAmalgamated")
                        label(for="showAmalgamated") Show on map

            #map
        #bottom
            | Developed by <a href="https://hire.stevebennett.me">Steve Bennett</a> for <a href="https://frontiersi.com.au/">Frontier SI</a>.
</template>

<script>
export default {
    name: 'app',
    components: {},
    data() {
        return {
            floorArea: undefined,
            amalgamatedFloorArea: undefined,
            showSeparate: true,
            showAmalgamated: true,
            setbacks: [
                { x: 2, h: 0 },
                { x: 3, h: 5 },
                { x: 4, h: 15 },
                { h: 30 },
            ],
            minFloorArea: 100, //, precinct: undefined
        };
    },
    watch: {
        setbacks(newSetbacks) {
            precinctChanged();
        },
        showSeparate(shown) {
            map.setLayoutProperty(
                'separateZiggurat',
                'visibility',
                shown ? 'visible' : 'none'
            );
        },
        showAmalgamated(shown) {
            map.setLayoutProperty(
                'amalgamatedZiggurat',
                'visibility',
                shown ? 'visible' : 'none'
            );
        },
    },
    methods: {
        recalculate() {
            precinctChanged();
        },
    },
    mounted() {
        window.app = this;

        initMap();
    },
};

const { fetch } = require('whatwg-fetch');
const Pbf = require('pbf');
const geobuf = require('geobuf');
const mapboxgldraw = require('@mapbox/mapbox-gl-draw');
const mapboxgl = require('mapbox-gl');
const turf = require('@turf/turf'); // TODO use only the modules we need
const makeZiggurat = require('./makeZiggurat');

// import jamsession from '@mapbox/expression/jamsession';
const jam = require('@mapbox/expression-jamsession').formulaToExpression;
function updateParcelSelection(parcels, precinct) {
    let selectedCount = 0;
    const selectedParcels = [];
    turf.featureEach(parcels, (parcel, id) => {
        let selected = precinct
            ? !turf.booleanDisjoint(parcel, precinct)
            : false;
        map.setFeatureState(
            {
                id: id,
                source: 'parcels',
            },
            {
                selected: selected,
            }
        );
        parcel._selected = selected;
        selectedCount += selected ? 1 : 0;
        if (selected) {
            selectedParcels.push(parcel);
        }
    });
    console.log(selectedCount + ' parcels found within precinct');
    return selectedParcels;
}

function precinctChanged(e, created) {
    if (drawControl.getAll().features.length) {
        // user has drawn a new polygon. Else use the old one.
        precinct = turf.combine(drawControl.getAll()); // multiple polygons to a single multipolygon
    }
    if (!precinct || !precinct.features.length) {
        // probably clicked the update button but never drew a polygon
        return;
    }
    if (precinct) {
        console.log(
            '' +
                precinct.features[0].geometry.coordinates[0].length -
                1 +
                ' vertices.'
        );
    }
    const selectedParcels = updateParcelSelection(parcels, precinct);

    const ziggurat = makeZiggurat.makeZiggurat(
        selectedParcels,
        window.app.setbacks,
        window.app.minFloorArea
    );
    map.U.update('ziggurat', ziggurat);
    window.app.floorArea = makeZiggurat.totalFloorArea(ziggurat);

    const amalgamated = [turf.union(...selectedParcels)];
    const amalgamatedZiggurat = makeZiggurat.makeZiggurat(
        amalgamated,
        window.app.setbacks,
        window.app.minFloorArea
    );
    window.app.amalgamatedFloorArea = makeZiggurat.totalFloorArea(
        amalgamatedZiggurat
    );
    map.U.update('amalgamatedZiggurat', amalgamatedZiggurat);
    if (created) {
        drawControl.deleteAll();
    }
}
let drawControl, parcels, map, precinct;
const dev =
    (window.location.host.match(/localhost/) &&
        !window.location.hash.match(/prod/)) ||
    window.location.hash.match(/dev/);
const devStyle = { version: 8, sources: {}, layers: [] };

function initMap() {
    mapboxgl.accessToken =
        'pk.eyJ1Ijoic3RldmFnZSIsImEiOiJGcW03aExzIn0.QUkUmTGIO3gGt83HiRIjQw';
    map = new mapboxgl.Map({
        container: 'map', // container id
        // style: 'mapbox://styles/mapbox/streets-v9', // stylesheet location
        style: dev
            ? devStyle
            : 'https://api.maptiler.com/maps/bright/style.json?key=WiUMgs9DgAoPMp824LNB',
        center: [145.2726, -37.816], // starting position [lng, lat]
        bearing: 68,
        // pitch: 57,
        zoom: dev ? 16 : 13, // starting zoom
    });
    require('mapbox-gl-utils').init(map);
    window.map = map;
    map.U.onLoad(() => {
        map.U.addGeoJSON('parcels').addLine('parcels-outline', {
            lineColor: 'hsl(240,50%,80%)',
        });
        map.U.addGeoJSON('ziggurat').addFillExtrusion('separateZiggurat', {
            fillExtrusionColor: {
                //'hsl(180, 80%, 50%)',
                property: 'height',
                stops: [
                    [0, 'hsl(120, 60%,40%)'],
                    [1, 'hsl(210, 95%,60%)'],
                    [30, 'hsl(230, 95%,80%)'],
                ],
            },
            fillExtrusionOpacity: 0.9,
            fillExtrusionHeight: ['get', 'height'],
        });

        map.U.addGeoJSON('amalgamatedZiggurat').addFillExtrusion(
            'amalgamatedZiggurat',
            {
                fillExtrusionColor: 'grey',
                fillExtrusionOpacity: 0.5,
                fillExtrusionHeight: ['+', ['get', 'height'], 0.5],
            }
        );
    });
    drawControl = new mapboxgldraw({
        displayControlsDefault: false,
        controls: {
            polygon: true,
            trash: true,
        },
    });
    map.addControl(drawControl, 'top-left');
    map.on('draw.create', (e) => precinctChanged(e, true));
    // map.on('draw.delete', precinctChanged);
    // map.on('draw.update', precinctChanged);

    fetch('data/out.geobuf')
        .then((response) => {
            return response.arrayBuffer();
        })
        .then((buffer) => {
            parcels = geobuf.decode(new Pbf(buffer));
            map.U.onLoad(() => map.U.update('parcels', parcels));
        });
}
</script>
<style src="../node_modules/mapbox-gl/dist/mapbox-gl.css">
</style>
<style src="../node_modules/@mapbox/mapbox-gl-draw/dist/mapbox-gl-draw.css">
</style>

